*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  /* overflow: hidden; */
}

.wordContainer {
  min-height: 89vh;
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: auto;
  font-family: 'Playfair Display', serif;
  background-size: cover no-repeat no-repeat;
  background: #bdc3c7;  
  /* fallback for old browsers */ 
   /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #2c3e50, #bdc3c7); 
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
 
h1 {
  text-align: center;
  font-size: 3em;
  font-family: 'Pacifico', cursive;
  color: #36317a;
  padding: 0.5em;
}
 
h2{
  font-size: 30px;
  text-decoration: underline;
  padding-bottom: 20px;
  margin-top: 10px;
  
}
 
h4{
  color: #36317a;
  font-weight: bolder;
  font-size: 1.2em;
  text-align: left;
  margin: 15px 0;
}

p{
  margin:5px 0;
}
.inputClass{
  display: flex;
  justify-content: center;
  align-items: center;
}
input{
  width: 400px;
  height: 38px;
  font-size: 20px;
  padding-left: 10px;
  border: 5px solid #36317a;
  border-radius: 40px;
}
 
.inputClass > button{
  background-color: #36317a;
  height: 38px;
  width: 60px;
  border: none;
  color: white;
  box-shadow: 0px 3px 2px #414789;
  cursor: pointer;
  padding: 10px  0;
  border-radius: 50%;
  margin-left: 20px;
}
 
.showResults{
  width: 500px;
  margin: 30px auto;
  padding: 20px;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}
 
.showResults > h2 > button{
  border-radius: 30px;
  cursor: pointer;
  background-color: #24214d;
}

#synonym{
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#antonym{
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
}
pre{
  font-family: 'Playfair Display', serif;
  display: flex;
  flex-direction: row;
  background-color:#36317a;
  margin:2px;
  border-radius: 5px;
  padding:4px;
  color:white;
  justify-content: center;
  flex-wrap: wrap;
}

#partsOfSpeech{
  font-size: 1.5em;
  font-weight: bolder;
  color: #24214d;
}

.wordNotFoundError{
  margin:40px;
  padding: 20px;
  color:white;
  background-color: #b01d1d;
  border:4px solid rgb(243,123,123);
  border-radius: 20px;
}

/* For iPads */

@media all and (max-width:700px)and (min-width:601px){
  h1{
    font-size: 2.7em;
    padding:0.37em;
  }

  .showResults{
    width:475px
  }
  h2{
    font-size: 24px;
    padding-bottom: 15px;
  }

  #partsOfSpeech{
    font-size: 1.3em;
  }
  h4{
    font-size: 1.1em;
  }
  p,pre{
